/* eslint-disable react-native/no-inline-styles */
import React from 'react';
import {useExamResultStore} from '../stores';
import {useEffect} from 'react';
import {
  FlatList,
  useWindowDimensions,
  RefreshControl,
  StyleSheet,
  View,
} from 'react-native';
import {
  useAuthStore,
  buildLink,
  IStudentExams,
} from '@smarttestpro/smart-common';
import {List, Text, useTheme} from 'react-native-paper';
import {TabBar, TabView} from 'react-native-tab-view';
import {useSafeAreaInsets} from 'react-native-safe-area-context';
import {YogaAnimation} from '../lotties';
import {ClockIcon, MathIcon, NumberQuizIcon} from '../icons';
import {formatDateTime} from '../DashboardFn';
import {InfoIcon} from '../icons/InfoIcon';
import {CalendarIcon} from '../icons/CalendarIcon';

function getRandomPhrase() {
  const phrases = [
    'Well done!',
    'Good job!',
    'Excellent work!',
    'Terrific effort!',
    'Fantastic!',
    'Great work!',
    'Bravo!',
    'Amazing job!',
    'Keep up the good work!',
    'You nailed it!',
    'Way to go!',
    "You're doing great!",
    'Impressive work!',
  ];

  const randomIndex = Math.floor(Math.random() * phrases.length);
  return phrases[randomIndex];
}
const SecondRoute = () => (
  <View
    style={{
      flex: 1,
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: '#fff',
    }}>
    <YogaAnimation />
    <Text>{getRandomPhrase()}</Text>
  </View>
);

const SListExamsScreenTab = ({navigation}: any) => {
  const {listStudentExams, loading, getListStudentExams} = useExamResultStore();
  const {user, accessToken, refreshToken} = useAuthStore();
  const theme = useTheme();
  useEffect(() => {
    if (listStudentExams.length === 0) {
      getListStudentExams(user.preferred_username);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onRefresh = () => {
    getListStudentExams(user.preferred_username);
  };

  const renderItem = ({item}: {item: IStudentExams}) => (
    <List.Item
      title={item.noiDungDeThi}
      description={
        <View style={{display: 'flex', flexDirection: 'column'}}>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              gap: 4,
            }}>
            <NumberQuizIcon
              width={16}
              height={16}
              color={theme.colors.secondary}
            />
            <Text style={{color: theme.colors.secondary}}>
              {item.soCau} câu -
            </Text>
            <ClockIcon color={theme.colors.secondary} width={16} height={16} />
            <Text style={{color: theme.colors.secondary}}>
              {item.thoiGian.trim()} phút
            </Text>
          </View>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              gap: 4,
            }}>
            <CalendarIcon
              width={16}
              height={16}
              color={theme.colors.secondary}
            />
            <Text style={{color: theme.colors.secondary}}>
              {formatDateTime(item.ngayBatDau)}
              {' - '}
              {formatDateTime(item.ngayKetThuc)}
            </Text>
          </View>
        </View>
      }
      onPress={() => {
        const baseUrl = 'https://smarttest.pro';
        const url = `/${buildLink('test-taking/exam/:id', {
          id: item.maDeThi,
        })}`;
        const uri = `${baseUrl}/thanh-vien/dang-nhap?return_to=${encodeURI(
          url,
        )}&accessToken=${encodeURI(accessToken || '')}&refreshToken=${encodeURI(
          refreshToken || '',
        )}`;
        navigation.push('TestExamWebScreen', {
          uri,
        });
      }}
      style={{
        borderBottomColor: theme.colors.elevation.level5,
        borderBottomWidth: 1,
      }}
      left={() => (
        <View style={styles.listIcon} pointerEvents="box-none">
          <MathIcon width={48} height={48} />
        </View>
      )}
    />
  );

  return (
    <View style={styles.container}>
      <FlatList
        data={listStudentExams}
        renderItem={renderItem}
        keyExtractor={item => item.maDeThi}
        refreshControl={
          <RefreshControl
            refreshing={loading === 'loading'}
            onRefresh={onRefresh}
          />
        }
      />
    </View>
  );
};

const SListExamsScreen = ({navigation}: any) => {
  const insets = useSafeAreaInsets();
  const layout = useWindowDimensions();
  const theme = useTheme();

  const [index, setIndex] = React.useState(0);

  const [routes] = React.useState([
    {key: 'first', title: 'Bài thi'},
    {key: 'second', title: 'Chưa thi'},
  ]);

  const renderScene = ({route}: {route: {key: string; title: string}}) => {
    switch (route.key) {
      case 'first':
        return <SListExamsScreenTab navigation={navigation} />;
      case 'second':
        return <SecondRoute />;
      default:
        return null;
    }
  };

  const renderTabBar = (props: any) => (
    <TabBar
      {...props}
      indicatorStyle={{backgroundColor: 'white'}}
      style={{backgroundColor: theme.colors.primary}}
    />
  );

  return (
    <TabView
      style={{paddingTop: insets.top, backgroundColor: theme.colors.primary}}
      navigationState={{index, routes}}
      renderScene={renderScene}
      renderTabBar={renderTabBar}
      onIndexChange={setIndex}
      initialLayout={{width: layout.width}}
    />
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    // paddingHorizontal: 4,
  },
  listIcon: {
    paddingLeft: 12,
    alignItems: 'center',
    justifyContent: 'center',
  },
});

export default SListExamsScreen;
